import { ethers } from 'ethers';
import { Magic } from 'magic-sdk';

class EthersClient {
  private static instance: EthersClient;

  private provider: ethers.providers.Web3Provider;

  private magic: any;

  private constructor(magic: any) {
    this.provider = new ethers.providers.Web3Provider(magic.rpcProvider);
    this.magic = magic;
  }

  public static getInstance(): EthersClient {
    if (!EthersClient.instance) {
      const m = new Magic(process.env.REACT_APP_MAGIC_SDK_KEY || '', { network: 'ropsten' });
      EthersClient.instance = new EthersClient(m);
    }
    return EthersClient.instance;
  }

  public getSigner() {
    return this.provider.getSigner();
  }

  public getUserInfos() {
    return this.magic.user.getMetadata();
  }

  public async login(email: string): Promise<string | null> {
    return this.magic.auth.loginWithMagicLink({ email });
  }

  public async logout(): Promise<boolean> {
    return this.magic.user.logout();
  }

  public async isUserLoggedIn(): Promise<boolean> {
    return this.magic.user.isLoggedIn();
  }
}

export default EthersClient;
