import { useState, useEffect } from 'react';
import ConfigCatClient from 'app/services/ConfigCatClient';

const useIsFeatureActivated = (featureName: string) => {
  const [isFeatureActivated, setIsFeatureActivated] = useState(false);
  const configCatSdkKey = process.env.REACT_APP_CONFIGCAT_SDK_KEY;
  const configCatClient = configCatSdkKey && ConfigCatClient.getInstance(configCatSdkKey);

  useEffect(() => {
    async function handleFeatureStatus() {
      if (configCatClient) {
        const feature = await configCatClient.getFeature(featureName);
        setIsFeatureActivated(feature);
      }
    }
    handleFeatureStatus();
  }, [configCatClient, featureName]);

  return isFeatureActivated;
};

export default useIsFeatureActivated;
