import React, { useState } from 'react';
import { Typography, Input, Button, Spin } from 'antd';
import { MailFilled, LoadingOutlined } from '@ant-design/icons';

import { ReactComponent as HeroImage } from 'assets/img/unlock.svg';
import isValidEmail from 'app/utils/isValidEmail';
import styles from './Login.module.css';

interface Props {
  onConnectionClick(email: string): void;
  loading: boolean;
}

const LoginScreen = ({ onConnectionClick, loading }: Props) => {
  const [email, setEmail] = useState('');
  const { Title } = Typography;
  return (
    <>
      <div className={styles.container}>
        <div className={styles.loginContainer}>
          <Title className={styles.title}>Welcome on Exorde.</Title>
          <Title className={styles.subtitle} level={5} type="secondary">
            Enter your email and we&apos;re ready to go.
          </Title>
          <Input
            size="large"
            placeholder="hubert@stonks.up"
            addonBefore={<MailFilled />}
            className={styles.input}
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <Spin indicator={<LoadingOutlined spin />} spinning={loading}>
            <Button
              size="large"
              type="primary"
              disabled={!isValidEmail(email)}
              className={styles.button}
              onClick={() => onConnectionClick(email)}>
              Let&apos;s go
            </Button>
          </Spin>
        </div>
        <div className={styles.heroImgContainer}>
          <HeroImage />
        </div>
      </div>
    </>
  );
};

export default LoginScreen;
