import * as configcat from 'configcat-js';

class ConfigCatClient {
  private static instance: any;

  private client: any;

  private constructor(sdkKey: string) {
    this.client = configcat.createClient(sdkKey);
  }

  public static getInstance(sdkKey: string): any {
    if (!ConfigCatClient.instance) {
      ConfigCatClient.instance = new ConfigCatClient(sdkKey);
    }
    return ConfigCatClient.instance;
  }

  public async getFeature(featureName: string) {
    return this.client.getValueAsync(featureName);
  }
}

export default ConfigCatClient;
